.custom-home-text-inverted {
  color: var(--color-text-home-header-default-2k-l2Z, #ffffff);
}

.custom-home-text-secondary {
  color: var(--color-text-home-header-secondary-1Theti, #d5dbdb);
}

@media (min-width: 992px) {
  .custom-home__sidebar {
    margin-top: -6rem;
  }
}
.custom-home-image__placeholder:before {
  content: "X";
  display: block;
  background-color: #f2f3f3;
  color: #ffffff;
  text-align: center;
  font-size: 40rem;
  line-height: 40rem;
}

.custom-list-separator {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.custom-list-separator li {
  border-top: 1px solid var(--color-border-divider-default-p5fVsz, #eaeded);
  padding: 0.8rem 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.custom-list-separator li:first-child {
  padding-top: 0;
  border-top: none;
}
.custom-list-separator li:last-child {
  padding-bottom: 0;
}